@font-face {
  font-family: poppins;
  src: url(./fonts/poppins-v5-latin-300.ttf);
}
* {
  padding: 0;
  margin: 0;
  font-family: poppins, Arial, Helvetica, sans-serif;
  box-sizing: border-box;
}

/* NavBar Styles  */
.shopzone--nav {
  position: fixed;
  z-index: 11111;
  width: 100%;
  display: flex;
  align-items: center;
  height: 80px;
  background-color: #f7f0e8;
  padding: 4px 80px;
  border-bottom: 1px solid rgba(148, 88, 148, 0.3);
  box-shadow: 0 5px 10px rgba(148, 88, 148, 0.3);
}
.shopzone--nav .logo {
  height: 75px;
  margin-right: 20px;
}
.shopzone--nav ul {
  margin-right: auto;
}
.shopzone--nav ul li {
  display: inline;
  padding: 0 15px;
}
.shopzone--nav ul li a {
  text-decoration: none;
  font-weight: bold;
  color: #1f4594;
  letter-spacing: 0.9px;
}
.shopzone--nav .search {
  width: 250px;
}

/* Categories in Navbar Styless */
.nav--category {
  position: absolute;
  top: 80px;
  left: 240px;
  z-index: 22;
  background-color: rgb(247, 240, 232);
}
.nav--category ul {
  display: flex;
  flex-direction: column;
  width: 200px;
}
.nav--category ul li {
  font-weight: bold;
  font-size: 15px;
  color: #1f4594;
  letter-spacing: 0.9px;
  border: 1px solid rgba(101, 116, 150, 0.7);
  border-top: none;
  border-radius: 4px;
  padding: 10px 0;
  cursor: pointer;
  text-align: center;
  display: flex;
  justify-content: center;
}

.search {
  width: 290px;
  padding: 6px;
  outline: none;
  border: 1px solid rgb(31, 69, 148);
  border-radius: 6px;
  background-color: transparent;
}
.search-btn {
  width: 20px;
  height: 20px;
  position: relative;
  left: -28px;
  margin-right: 10px;
}
.shopzone--nav .profile {
  width: 55px;
  height: 55px;
  background-image: url("logo.svg");
  background-position: center;
  background-size: cover;
  border-radius: 50px;
}

/* ShopZone Main Content Styles */
.shopzone--main .banner {
  position: relative;
  top: 80px;
  background-color: #f7f0e8;
  display: flex;
  padding: 70px 220px;
  align-items: center;
  justify-content: space-between;
}
.shopzone--main .banner--content {
  width: 55%;
  letter-spacing: 1.1px;
  font-size: 1.2em;
  font-weight: 900;
  text-align: center;
}
.banner--content h4 {
  padding: 10px 0 20px 0;
}
.shopzone--main .banner--img {
  display: flex;
  align-items: center;
}
#banner2 {
  width: 100%;
}
.shopzone--main .search-btn {
  top: 5px;
}

/* Slider  */
.slider {
  margin-top: 140px;
}
.title {
  text-transform: capitalize;
  font-size: 1.5em;
  font-weight: 900;
  letter-spacing: 1.1px;
  color: #1f4594;
  text-align: center;
  margin: 60px 0 20px 0;
}

/* Visitors Styles */
.visitors {
  margin-top: 60px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: #f7f0e8;
  padding: 30px 0;
  color: #1f4594;
  font-weight: bold;
}
.visitors--content {
  display: flex;
  flex-direction: column;
  border: 1px solid #1f4594;
  border-radius: 6px;
  margin: 2%;
  align-items: center;
  justify-content: center;
  padding: 40px 65px;
}
.visitors--content img {
  width: 50px;
  height: 50px;
  margin-bottom: 10px;
}

/* categories styles */
.categories {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.category {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 1.8% 2%;
  justify-content: center;
  width: 250px;
  cursor: pointer;
}
.category img {
  width: 250px;
  height: 140px;
  border-radius: 4px;
}
/* #0c1442 */
.category--name {
  font-size: 20px;
  font-weight: 900;
  letter-spacing: 1.2px;
  text-align: center;
  padding-bottom: 3px;
}
.category .stars img {
  width: 20px;
  height: 20px;
  padding: 1px;
}
.card--badge {
  position: absolute;
  top: 33px;
  right: 0px;
  background-color: rgba(255, 255, 255, 0.952);
  font-weight: bold;
  padding: 2px;
  width: 65px;
  text-align: center;
  font-size: 0.8em;
  border-radius: 2px;
}
.category h5 {
  text-align: justify;
}
.category .category--link {
  text-decoration: none;
  text-align: right;
  font-size: 0.9em;
  color: black;
  position: absolute;
  top: 153px;
  background-color: rgba(255, 255, 255, 0.852);
  width: 250px;
  text-align: center;
}
.displayNone {
  display: none;
}
/* Cards Styles */
.card {
  margin: 0 20%;
  flex: 0 0 auto;
  display: flex;
  align-items: center;
  border: 1px solid #1f4594;
  border-radius: 6px;
  padding: 20px;
  background-color: #f7f0e8;
}
.card--img {
  width: 160px;
  height: 240px;
  margin-right: 60px;
}
.card--img img {
  width: 180px;
  height: 100%;
  border-radius: 8px;
  cursor: pointer;
}
.location {
  text-transform: uppercase;
  letter-spacing: 2.5px;
  display: inline-block;
  line-height: 18px;
  margin-right: 20px;
}
.map--link {
  display: inline-block;
  text-decoration: underline;
  line-height: 18px;
  color: grey;
  font-weight: 500;
  cursor: pointer;
}
.card h1 {
  font-weight: 900;
  margin-bottom: 20px;
}
.date {
  font-weight: bold;
  margin-bottom: 10px;
}

/* Footer Styless */
.shopzone--footer {
  background-color: #0c1442;
  color: #fff;
  margin-top: 80px;
  padding: 20px 10px 0 10px;
}
.shopzone--footer .footer--contact {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding-bottom: 20px;
}
.shopzone--footer .footer--content {
  padding: 0 10px;
  display: flex;
  align-items: center;
}
.shopzone--footer img {
  width: 35px;
  height: 35px;
  margin: 5px 20px;
}
.call,
.our-location {
  border-right: 3px solid #fff;
}
.footer--social,
.footer--copyright {
  padding: 10px 0px;
  display: flex;
  justify-content: center;
}
.footer--social img {
  margin: 5px 10px;
}

@media screen and (max-width: 900px) {
  .shopzone--nav {
    padding: 0 0px;
    width: 100%;
    justify-content: center;
  }
  .shopzone--nav ul li {
    padding-right: 20px;
  }
  .shopzone--nav .search,
  .shopzone--nav .search-btn,
  .shopzone--nav .profile {
    display: none;
  }
  .shopzone--nav ul {
    margin-right: initial;
  }
  .shopzone--main .banner {
    flex-direction: column;
    justify-content: center;
    padding: 30px !important;
  }
  .shopzone--main .banner--content {
    font-size: 1em;
    width: 90% !important;
  }
  .card {
    margin: 0 15%;
  }
  .container {
    justify-content: space-between;
    display: flex;
  }
  .call,
  .our-location,
  .email-us {
    width: 60%;
    padding: 15px !important;
  }
  .call,
  .our-location {
    border-right: none;
    border-bottom: 3px solid #fff;
  }
  .footer--social {
    width: 20%;
    flex-direction: column;
  }
  hr.first {
    display: none;
  }
  .footer--copyright {
    float: initial;
  }
}

@media screen and (max-width: 786px) {
  .card {
    flex-direction: column;
    margin: 0 40px;
  }
  .card--img {
    width: 100%;
    height: 240px;
    margin: 5px 0;
  }
  .card--img img {
    width: 100%;
    height: 97%;
  }
  .call,
  .our-location,
  .email-us {
    width: 80%;
    padding: 15px !important;
    margin: 0 0px;
  }
  .container {
    /* justify-content: space-between; */
    display: block;
  }
  .footer--social {
    width: 100%;
    flex-direction: row;
  }
  hr.first {
    display: block;
  }
}
